article {
  color: #fff;
}

article section p.text-ashara {
  font-family: "Raleway Regular", "Arial Regular", sans-serif;
  font-size: 1rem;
  text-align: center;
  margin: 1% 5%;
}

.container-article {
  margin: auto;
  width: 80%;
  justify-content: center;
}

.container-socialNetwork {
  background-color: #fff;
  text-align: center;
  padding-top: 30px;
}

.socialNetwork-followUs {
  font-family: "Raleway Semibold", "Helvetica Semibold", "Arial semibold",
    sans-serif;
  font-size: 2rem;
  margin: 0;
  margin-bottom: 3%;
  color: #666;
}
.container-heroes {
  background-color: #fff;
  text-align: center;
  color: #666;
}

@media screen and (min-width: 425px) {
  article section p.text-ashara {
    font-size: 1.2rem;
    margin: 2% 10%;
  }
}

@media screen and (min-width: 768px) {
  article section p.text-ashara {
    font-size: 1.25rem;
    text-align: center;
    margin: 2% 6%;
  }

  .container-article {
    display: flex;
    margin: auto;
    width: 85%;
  }
}

@media screen and (min-width: 1280px) {
  article section p.text-ashara {
    font-size: 1.7rem;
    text-align: center;
  }
  .container-article {
    width: 75%;
  }
}
